.theTable {
    width: 100%;
    /* background-color: yellow; */
    border-radius: 10px;
    border: 2px var(--border_color) solid;
    border-collapse: collapse;
}
.theTable thead {
    background-color: grey;
    /* border-radius: 10px; */
    border: 2px var(--border_color) solid;
    border-collapse: collapse;
}
.theTable td {
    /* background-color: red; */
    padding: 10px;
    /* border-radius: 10px; */
    text-align: center;
    border: 2px var(--border_color) solid;
    border-collapse: collapse;
}
.theTableTr:hover {
    background-color: rgb(181, 191, 195);
    cursor: pointer;
    /* scale: 1.05; */
    color: black;
}

.errorNoData {
    border: 2px red solid;
    padding: 15px;
    color: red;
    border-radius: 15px;
}